const initialState = {
    checkUser: localStorage.getItem("logged"),
    logout: true,
    medicines:JSON.parse(localStorage.getItem("medicines")) === null ? [] : JSON.parse(localStorage.getItem("medicines")),
    edit_medicine: {},
    team: JSON.parse(localStorage.getItem("executives")) === null ? [] :JSON.parse(localStorage.getItem("executives")),
    edit_executive: {},
    orders: JSON.parse(localStorage.getItem("orders")) === null ? [] : JSON.parse(localStorage.getItem("orders")),
    filteredOrders: []
}
const addReducers = (state = initialState,{type , payload}) => {
    switch(type){
        case "login": {
            return {...state, logout: payload}
        }
        case "edit_medicine" : {
            return {...state, medicines: payload}
        }
        case "medicine" : {
            return {...state, edit_medicine: payload}
        }
        case "edit_executive": {
            return {...state, team: payload}
        }
        case "executive" : {
            return {...state, edit_executive: payload}
        }
        case "edit_orders" : {
            return {...state, orders: payload}
        }
        case "filter": {
            return {...state, filteredOrders: payload}
        }
        default : {
            return state;
        }
    }
}
export default addReducers;