import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import { login } from "../../redux/action";
import "./style.css"
class Navbar extends PureComponent {
    constructor(props) {
        super(props);
    
    }
    logOut = () => {
        localStorage.removeItem("logged");
        this.props.logouting(true);
    }
    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="ms-5 me-5 container-fluid">
                    <a className="navbar-brand" href="#">myMEDICINCES.com</a>
                    {this.props.logout  ? "" :
                        <div>
                            <button id="back" className="btn btn-info me-3">Back</button>
                            <Link to={"/home"}>
                            <button id="home" className="btn btn-warning me-3">home</button>
                            </Link>
                            <button id="logout" onClick={this.logOut} className="btn btn-danger">LogOut</button>
                        </div>
                    }
                    {this.props.logout && <Redirect to="/" />}
                </div>
            </nav>
        )
    }
}
const mapStateToProps = (state) => ({
    logout: state.logout
})
const mapDispatchToProps = (dispatch) => ({
    logouting: (payload) => dispatch(login(payload))
})
export default connect(mapStateToProps, mapDispatchToProps)(Navbar);